<template>
  <div id="app">
    <!-- 標題區域 -->
    <HeaderMenu
      :cities="cities"
      :towns="towns"
      :selectedCity="selectedCity"
      :selectedTown="selectedTown"
      @cityChange="handleCityChange"
      @townChange="handleTownChange"
    />

    <!-- 主要資料區域 -->
    <div class="main-content">
      <!-- 資料區 -->
      <div class="data-area">
        <ContentArea
          :cities="cities"
          :towns="towns"
          :selectedCity="selectedCity"
          :selectedTown="selectedTown"
          :currentPage="currentPage"
          :totalPages="totalPages"
          :pagedData="pagedData"
          @gotoPage="gotoPage"
          :loading="loading"
        />
      </div>

      <!-- 右側廣告 -->
      <div class="advertisement-area">
        <AdvertisementBlock :advertisements="advertisements" />
      </div>
    </div>

    <!-- 葉腳 -->
    <FooterInfo />
  </div>
</template>

<script>
import axios from "axios";
import HeaderMenu from "./HeaderMenu.vue";
import ContentArea from "./ContentArea.vue";
import AdvertisementBlock from "./Advertisement.vue";
import FooterInfo from "./FooterInfo.vue";

export default {
  name: "App",
  components: {
    HeaderMenu,
    ContentArea,
    AdvertisementBlock,
    FooterInfo
  },
  data() {
    return {
      selectedCity: null,
      selectedTown: null,
      allData: [],
      cities: [],
      towns: {},
      currentPage: 1,
      itemsPerPage: 10,
      loading: false,
      advertisements: []
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    filteredData() {
      return this.allData.filter(item => (
        (!this.selectedCity || item.City === this.selectedCity) &&
        (!this.selectedTown || item.Town === this.selectedTown)
      ));
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredData.slice(start, end);
    },
  },
  watch: {
    selectedCity(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.currentPage = 1;
        this.updateTotalPages();
      }
    },
    selectedTown(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.currentPage = 1;
        this.updateTotalPages();
      }
    },
  },
  created() {
    this.fetchData();
    this.advertisements = [
      { src: require("./assets/adv1.png") },
      { src: require("./assets/adv2.png") },
      { src: require("./assets/adv3.png") }
    ];
  },
  methods: {
    updateTotalPages() {
      this.totalPages = Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get("https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx");
        this.allData = response.data.map(item => ({
          ID: item.ID,
          Name: item.Name,
          City: item.City,
          Town: item.Town,
          FoodFeature: item.FoodFeature,
          PicURL: item.PicURL,
          URL: item.URL
        }));
        this.updateCitiesAndTowns();
        this.totalPages = Math.ceil(this.filteredData.length / this.itemsPerPage); // 重新計算 total Pages
      } catch (error) {
        console.error("There was an error fetching the data: ", error);
      } finally {
        this.loading = false;
      }
    },
    updateCitiesAndTowns() {
      this.cities = [...new Set(this.allData.map(item => item.City))];
      this.towns = this.allData.reduce((acc, item) => {
        if (!acc[item.City]) {
          acc[item.City] = [];
        }
        if (!acc[item.City].includes(item.Town)) {
          acc[item.City].push(item.Town);
        }
        return acc;
      }, {});
    },
    handleCityChange(city) {
      this.selectedCity = city;
      this.currentPage = 1;
    },
    handleTownChange(town) {
      this.selectedTown = town;
      this.currentPage = 1;
    },
    gotoPage(pageNumber) {
      this.currentPage = pageNumber;
    },
  },
};
</script>

