<template>
  <div>
    <!-- 左側區塊 -->
    <div class="left-content">
      <!-- 左側內容 -->
      <!-- 在這裡放置左側區塊的內容 -->
    </div>

    <!-- content 和廣告包裹 -->
    <div class="content-ad-wrapper">
      <!-- 餐廳容器 -->
      <div class="restaurant-container">
        <!-- 下拉選單 -->
        <div class="dropdowns">
          <select v-model="selectedCity" @change="handleCityChange" class="dropdown">
            <option value="" disabled selected>請選擇縣市</option>
            <option v-for="city in cities" :key="city" :value="city">{{ city }}</option>
          </select>

          <select v-model="selectedTown" @change="handleTownChange" class="dropdown">
            <option value="" disabled selected>請選擇鄉鎮市</option>
            <template v-if="selectedCity">
              <option v-for="town in towns[selectedCity]" :key="town" :value="town">{{ town }}</option>
            </template>
          </select>
        </div>

        <!-- 餐廳列表 -->
        <div class="content-wrapper">
          <!-- 加載指示器和錯誤訊息 -->
          <div v-if="loading" class="loading-indicator">加載中...</div>
          <div v-if="error" class="error-message">{{ error }}</div>

          <!-- 餐廳項目 -->
          <div v-for="item in pagedData" :key="item.ID" class="restaurant-item">
            <!-- 位置信息 -->
            <div class="location-info">
              <div class="city-label highlight-city">{{ item.City }}</div>
              <div class="town-label">{{ item.Town }}</div>
            </div>
            <!-- 餐廳內容 -->
            <div class="restaurant-content">
              <div class="restaurant-image">
                <div class="image-container">
                  <img :src="item.PicURL" alt="Restaurant Image">
                </div>
              </div>
              <div class="restaurant-info">
                <div class="restaurant-title">
                  <div class="restaurant-name" :style="{ color: item.URL ? 'rgb(12, 164, 211)' : 'black' }" @click="openUrl(item.URL)">{{ item.Name }}</div>
                  <div class="restaurant-description" v-html="item.FoodFeature.replace(/<br><br>/g, '')"></div>
                </div>
              </div>
            </div>
          </div>

          <!-- 分頁控件 -->
<div class="pagination" v-if="totalPages > 1 || filteredData.length === 0">
  <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
  <div class="page-buttons">
    <button v-for="pageNumber in totalPages" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">{{ pageNumber }}</button>
  </div>
</div>
        </div>
      </div>
    </div>
    <!-- AdvertisementBlock 组件 -->
    <AdvertisementBlock :advertisements="advertisements" :isAdFixed="isAdFixed" />
  </div>
</template>



<script>
import axios from "axios";

export default {
  name: "ContentArea",
  props: {
    cities: Array,
    towns: Object,
  },
  data() {
    return {
      allData: [],
      currentPage: 1,
      itemsPerPage: 10,
      loading: false,
      error: null,
      selectedCity: "", // 預設為空字串
      selectedTown: "", // 預設為空字串
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    filteredData() {
      // 確認選擇的縣市和鄉鎮市是否為空，若鄉鎮市為空，則使用先前選擇的縣市作為篩選依據
      const city = this.selectedTown ? this.selectedCity : this.selectedCity || '';
      const town = this.selectedTown || '';
      return this.allData.filter(
        (item) =>
          (!city || item.City === city) &&
          (!town || item.Town === town)
      );
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredData.slice(start, end);
    },
  },
  watch: {
    selectedCity(newVal, oldVal) {
      if (newVal !== oldVal && newVal !== "") { // 確保選擇的不是提示文字
        this.currentPage = 1;
        this.fetchData();
      }
    },
    selectedTown(newVal, oldVal) {
      if (newVal !== oldVal && newVal !== "") { // 確保選擇的不是提示文字
        this.currentPage = 1;
        this.fetchData();
      }
    },
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get(
          "https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx"
        );
        this.allData = response.data.map((item) => ({
          ID: item.ID,
          Name: item.Name,
          City: item.City,
          Town: item.Town,
          FoodFeature: item.FoodFeature,
          PicURL: item.PicURL,
          URL: item.URL,
        }));
      } catch (error) {
        console.error("获取数据时发生错误: ", error);
        this.error = "获取数据时发生错误，请稍后重试。";
      } finally {
        this.loading = false;
      }
    },
    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    handleCityChange(event) {
      this.selectedCity = event.target.value;
      this.selectedTown = ""; // 重置鄉鎮市選擇
    },
    handleTownChange(event) {
      this.selectedTown = event.target.value;
    },
    openUrl(url) {
      if (url) {
        window.open(url, "_blank");
      }
    },
  },
};
</script>





<style lang="scss">
/* 主要内容区 */
.content-ad-wrapper {
  display: flex;
}

.dropdown {
  appearance: none; /* 清除系統默認樣式 */
  border: 2px solid #3498db; /* 添加邊框 */
  background-color: #fff; /* 背景顏色 */
  color: #3498db; /* 文字顏色 */
  padding: 10px; /* 內邊距 */
  font-size: 16px; /* 字體大小 */
  cursor: pointer; /* 鼠標指示器 */
  transition: border-color 0.3s, color 0.3s; /* 添加過渡效果 */
}

.dropdown:hover,
.dropdown:focus {
  border-color: #2980b9; /* 滑鼠懸停時和聚焦時的邊框顏色 */
}

.dropdown option {
  background-color: #fff; /* 選項背景顏色 */
  color: #3498db; /* 選項文字顏色 */
  font-size: 16px; /* 選項字體大小 */
}

.dropdown:disabled {
  opacity: 0.5; /* 禁用時的透明度 */
  cursor: not-allowed; /* 禁用時的鼠標指示器 */
}


.restaurant-container {
  margin-left: 20px;
  flex-grow: 1;
}

.restaurant-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.location-info {
  margin-right: 0px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.city-label {
  display: block;
  margin-bottom: 5px;
  background-color: rgb(12, 164, 211);
  color: white;
  padding: 2px 4px;
  border-radius: 4px;
}

.city-label.highlight-city {
  display: block !important;
  white-space: nowrap; /* 防止文字换行 */
}


.town-label {
  display: block;
  writing-mode: vertical-rl; /* 垂直显示 */
  white-space: nowrap; /* 防止文字换行 */
  color: #999999;
  font-size: 16px; /* 调整文字大小，根据需要进行调整 */
  line-height: 1.5; /* 调整行高，根据需要进行调整 */
  margin-right: 10px; /* 向左移动一定距离，根据需要进行调整 */
  letter-spacing: 5px; /* 字体间距 */
}

.restaurant-content {
  display: flex;
  align-items: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  box-sizing: border-box;
}

.restaurant-image {
  margin-right: 20px;
}

.restaurant-info {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.restaurant-description { /* 名片文字 */
  font-size: 16px;
  line-height: 1.6;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  white-space: normal;
  margin-top: 10px; /* 增加描述和上方元素的距離 */
}

.restaurant-title {
  margin-bottom: 15px;
}

.restaurant-title .restaurant-name {
  font-weight: bold;
  font-size: 26px;
  text-align: left;
  color: black; /* 更改為黑色 */
  transition: color 0.3s ease;
}

.restaurant-item:hover .restaurant-title .restaurant-name {
  color: rgb(12, 164, 211); /* 滑鼠懸停時的顏色 */
}


.pagination {
  display: flex;
  justify-content: space-between; /* 左右对齐 */
  align-items: center; /* 垂直居中对齐 */
}

.page-info {
  margin-right: 10px;
}

.page-buttons {
  display: flex;
}

.page-button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid #ccc; /* 边框颜色 */
  background-color: #ccC; /* 背景颜色 */
  color: white; /* 文字颜色 */
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s; /* 过渡效果 */
}

.page-button:hover:not(:disabled) {
  background-color: #e0e0e0; /* 鼠标悬停时的背景颜色 */
  color: white; /* 文字颜色 */
}

.page-button:disabled {
  cursor: not-allowed;
  color: #999; /* 禁用按钮的文字颜色 */
}

.page-button.active {
  background-color: rgb(12, 164, 211); /* 激活按钮的背景颜色 */
  font-weight: bold;
  color: white; /* 激活按钮的文字颜色 */
}

.image-container {
  max-width: 200px;
  max-height: 150px;
  overflow: hidden;
}

.image-container img {
  width: 100%;
  height: auto;
}

.loading-indicator {
  text-align: center;
  font-size: 18px;
  margin: 20px 0;
}

.error-message {
  text-align: center;
  color: red;
  font-size: 18px;
  margin: 20px 0;
}
</style>
